/* Global styles */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
  }
  
  :root {
    --primary-color: #028cc4;
    --secondary-color: #b1d1ef;
    --text-color: #004d85;
    --odd-row-color: #f8f9fa;
    --even-row-color: #ffffff;
  }
  
  /* Modern redesign styles */
  .leaderboardPage-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #b6ddfd; /* Baby blue background color */
  }
  
  .leaderboard-container {
    max-width: 90%;
    margin: 30px auto;
    padding: 20px;
    background: var(--secondary-color);
    border-radius: 30px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    color: var(--text-color);
    animation: slideIn 0.8s ease, fadeIn 1s ease;
  }
  .leaderboard-container h2 {
    animation-delay: 0.5s;
    color: #003455;
    font-weight: bold;
    text-align: center;
  }
  
  /* Animation for the leaderboard container */
  @keyframes slideIn {
    from {
      transform: translateY(-50%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* Animation for the leaderboard appearance */
  .appear-animation {
    animation: appear 1s ease;
  }
  
  /* Animation for the leaderboard content */
  @keyframes appear {
    from {
      transform: translateY(-20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  .leaderboard-titles {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    align-items: center;
    padding: 10px;
    background-color: var(--primary-color);
    color: white;
    border-radius: 5px;
    font-weight: bold;
  }
  
  .leaderboard-title {
    text-align: center;
  }
  
  .leaderboard-content {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .leaderboard-row {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    align-items: center;
    padding: 10px;
    border-radius: 5px;
    background-color: var(--even-row-color);
    transition: background-color 0.3s;
  }
  
  .leaderboard-row:nth-child(odd) {
    background-color: var(--odd-row-color);
  }
  
  /* Hover effect for leaderboard rows */
  .leaderboard-row:hover {
    background-color: var(--primary-color);
    color: white;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .leaderboard-container {
      max-width: 95%;
      margin: 20px auto;
      padding: 15px;
    }
  }
  
  @media (max-width: 480px) {
    .leaderboard-container {
      max-width: 100%;
      margin: 10px auto;
      padding: 10px;
    }
  
    .leaderboard-titles {
      font-size: 14px;
    }
  
    .leaderboard-title {
      padding: 5px;
    }
  
    .leaderboard-content {
      gap: 5px;
    }
  
    .leaderboard-row {
      font-size: 14px;
      padding: 5px;
    }
  }
  
  @media (max-width: 360px) {
    .leaderboard-titles {
      font-size: 12px;
    }
  
    .leaderboard-title {
      padding: 3px;
    }
  
    .leaderboard-row {
      font-size: 12px;
      padding: 3px;
    }
  }
  